import React from 'react';
import { Prompt } from 'react-router-dom';
import '../styles/ContactPage.css';

class ContactPage extends React.Component {

    state = {
        value: "",
        isEmpty: true
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({
            value: ""
        });
    }

    handleChange = (e) => {
        if(e.target.value.length > 0) {
            this.setState({
                value: e.target.value,
                isEmpty: false
            });
        } else {
            this.setState({
                value: e.target.value,
                isEmpty: true
            });
        }
    }

    render() {
        return (
            <div className="contact">
                <form>
                    <h3>Napisz do nas!</h3>
                    <textarea value={this.state.value} onChange={this.handleChange} placeholder="Wpisz wiadomo≈õƒá"></textarea>
                    <button onClick={this.handleSubmit}>Wy≈õlij</button>
                </form>

                <Prompt
                    when={!this.state.isEmpty}
                    message="Masz niewype≈Çniony formularz. Czy na pewno chcesz opu≈õciƒá tƒÖ stronƒô?"
                />
            </div>
        );
    }
}
export default ContactPage;